<template >
<main>
    <Header :header='content'/>

<h5 style="text-align:center;color:green;"> برای مثال کد 12345 وارد شود</h5>
    <div>
    <p>
        <input type="text" v-model="barcode" placeholder="کد قطعه مورد نظر را وارد نمایید ...">
        <button @click="getPart" class="btn btn-primary d-block mx-auto">دریافت</button>
    </p>
</div>
<p class="text-center text-danger">{{error}}</p>    
<form action="" class="container  mx-auto">
    <section> 
        <div> 
            <label>نام</label> 
            <input type="text" v-model="inpname">
        </div>
        <div>
            <label>برند</label> 
            <input type="text" v-model="inpbrand">
        </div>
    </section>
</form>

<div class="text-center  mt-5">
    <button class="btn btn-success "   @click="editPart">ویرایش و ارسال</button>
</div>

</main> 
</template>

<script>
import Header from '../../components/Header.vue';
import axios from 'axios'
export default {
    name:'ChangeParts',
    components:{Header},
    data() {
        return {
            content:"ویرایش اطلاعات قطعه",
            msg:"اطلاعات قطعه جدید با موفقیت ثبت گردید",
            partname:'',
            brand:'',
            inpname:'',
            inpbrand:'',
            barcode:'',
            error:''
            
        }
    },
    methods: {
        getPart(){
            if(this.barcode==='12345'){
            axios.get('../parts.json')
            .then((response)=>{
                this.inpname=response.data.partname;
                this.inpbrand=response.data.brand;
                this.barcode='';
                })
            
            }else{
                this.error='کد قطعه اشتباه وارد شده است';
                this.barcode='';
            }
        },
        editPart(){
            
            axios.put('../parts.json',{
            partname: this.inpname,
            brand: this.inpbrand
            });
            this.inpname='';
            this.inpbrand='';
        }
    }
}
</script>





<style lang="scss" scoped>
@import '../../style/2col-form.scss';

main{

    div{
        text-align: center;
            p{
                position: relative;
                display: inline-block;
                input{
                    
                    position:relative;
                    width: 30vw;
                    
                }
                button{
                    position: absolute;
                    left: 0;
                    top: 0;
                    bottom: 0;
                }

            }      

    }

    table{
        border-radius: 2px;
        background-color: rgb(255, 255, 255);
        width:80%;
        th{
            width: 15%;
        }
        td{
            text-align: center;
        }
    }
    
        

}
</style>
